@import url(https://fonts.googleapis.com/css?family=Rubik);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  font-family: 'Rubik', sans-serif;
  font-size: 1em;
  line-height: 1.375em; }

.image-background {
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  height: 550px; }

.poster {
  width: auto;
  height: 220px;
  margin-top: -110px;
  margin-left: 8%;
  border: 2px solid #ffffff; }

.movieshow-title {
  margin: 25px;
  font-size: 2.8em;
  font-weight: 300;
  line-height: 1.05em;
  position: absolute; }

.card-deck {
  max-width: 1200px;
  margin: 0 auto !important;
  padding-bottom: 50px; }

.card {
  min-width: 250px !important;
  max-width: 350px !important;
  margin: 20px; }

.card a {
  text-decoration: none;
  color: black; }
  .card a:hover {
    color: grey; }

.card-img-top {
  height: 200px;
  object-fit: cover; }

.map {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin: 0 auto 70px auto !important;
  max-width: 1200px; }

/* Device = Tablets, Ipads (portrait) Screen = B/w 768px to 1024px*/
@media (min-width: 768px) and (max-width: 1024px) {
  .image-background {
    height: 450px; } }

/* Device = Tablets, Ipads (landscape) Screen = B/w 768px to 1024px*/
@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {
  .image-background {
    height: 350px; }
  .poster {
    height: 180px;
    margin-top: -95px; }
  .movieshow-title {
    margin: 15px 0 0 20px;
    font-size: 2em; } }

/* Device = Low Resolution Tablets, Mobiles (Landscape) Screen = B/w 481px to 767px*/
@media (min-width: 481px) and (max-width: 767px) {
  .image-background {
    height: 250px; }
  .poster {
    height: 140px;
    margin-top: -65px; }
  .movieshow-title {
    margin: 15px 0 0 20px;
    font-size: 1.8em; } }

/* Device = Most of the Smartphones Mobiles (Portrait) Screen = B/w 320px to 479px*/
@media (max-width: 480px) {
  .image-background {
    height: 200px; }
  .poster {
    height: 120px;
    margin-top: -65px; }
  .movieshow-title {
    margin: 15px 0 0 20px;
    font-size: 1.5em; } }

.home-background {
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  height: auto; }

.thumbnail {
  -webkit-transition: 0.15s ease-in-out;
  transition: 0.15s ease-in-out; }
  .thumbnail:hover {
    -webkit-transform: translateY(-10px) scale(1.02);
            transform: translateY(-10px) scale(1.02); }

.card-body h5 {
  text-align: center; }

input[type=search] {
  text-align: left;
  cursor: pointer;
  display: block;
  width: 100%;
  letter-spacing: 2px;
  border-radius: 10px; }

.box-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  margin: 50px; }

.box-container h3 {
  color: #a0a0a0; }

.box-container img {
  width: 350px; }

.register-container {
  margin: 20px;
  padding: 20px;
  max-width: 500px;
  border-radius: 20px;
  border: 1px solid #bbb7b7; }

.box-container label {
  margin-top: 10px; }

.button {
  margin-top: 20px;
  background-color: #74E0D4;
  color: white;
  border-radius: 10px;
  border: 1px solid #74E0D4;
  padding: 7px 15px; }
  .button:hover {
    background-color: white;
    color: #74E0D4; }

nav {
  background-color: #143340;
  color: white; }

nav ul {
  text-decoration: none; }

nav li span {
  padding: 0 0px;
  color: white;
  list-style: none;
  margin-right: 20px; }
  nav li span:hover {
    color: #FA9028; }

.navbar-brand img {
  width: 150px; }

nav li .button {
  background-color: #FA9028;
  color: white;
  border-radius: 10px;
  border: 1px solid #FA9028;
  padding: 5px 10px; }
  nav li .button:hover {
    background-color: #f8a757;
    color: white; }

nav li button {
  background-color: transparent;
  border: 0px;
  margin: 0px;
  padding: 0px; }

.photo img {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  object-fit: cover;
  position: absolute;
  margin-top: -90px;
  margin-left: -100px; }

.photo {
  position: relative;
  text-align: center;
  margin-bottom: 100px; }

.button-file {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background-color: yellow;
  position: absolute;
  opacity: 0;
  overflow: hidden;
  z-index: 1000;
  margin-top: -90px;
  margin-left: -100px; }

.profile-container {
  background-image: url("/images/pattrn.jpeg");
  height: 170px; }

.profile-box {
  text-align: center; }

.profile-box h2 {
  text-align: center;
  font-size: 2.25em;
  line-height: 1.25;
  padding-top: 40px;
  color: white; }

.profile-box h5 {
  text-align: left;
  width: 40%;
  color: white;
  padding: 5px 10px 5px 10%;
  margin-top: 230px;
  background-color: #74E0D4; }

@media (min-width: 481px) and (max-width: 767px) {
  .profile-box h5 {
    width: 55%; } }

@media (max-width: 480px) {
  .profile-box h5 {
    width: 100%; } }

img {
  width: 100px; }

.carousel-item {
  height: 500px; }

.image-carrusel {
  width: 100%;
  height: 100%;
  object-fit: cover; }

.info-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row; }

.left-container {
  padding: 20px;
  -webkit-flex-basis: 50%;
          flex-basis: 50%; }

.title-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row; }
  .title-container h1 {
    font-size: 3em;
    font-weight: 300;
    line-height: 1.05em;
    padding: 10px 5px; }

.left-container h4 {
  font-size: 1.125em;
  line-height: 1.22222222em;
  padding: 10px 5px;
  color: #74E0D4; }

.left-container p {
  text-align: justify;
  padding: 10px 5px; }

.title-container button {
  margin-left: 20px; }

.right-container {
  padding: 20px;
  -webkit-flex-basis: 50%;
          flex-basis: 50%; }

.media {
  margin: 15px 30px;
  background-color: #f4f7f9;
  padding: 10px;
  border-radius: 5px; }

.media img {
  border-radius: 5px; }

.media-body h6 {
  font-weight: 700; }

.media-body h5 {
  font-weight: 800; }

.button-fav {
  background-color: transparent;
  border: none;
  margin-top: 15px;
  width: 45px;
  height: 45px; }

.button-fav img {
  width: 20px; }

/* Device = Low Resolution Tablets, Mobiles (Landscape) Screen = B/w 481px to 767px*/
@media (min-width: 481px) and (max-width: 767px) {
  .info-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column; }
  .carousel-item {
    height: 350px; } }

/* Device = Most of the Smartphones Mobiles (Portrait) Screen = B/w 320px to 479px*/
@media (max-width: 480px) {
  .info-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column; }
  .carousel-item {
    height: 200px; } }

.comment-container {
  margin: 20px 30px 5px 30px;
  background-color: #f4f7f9;
  padding: 20px;
  border-radius: 5px; }

.comment-container input {
  border: none; }

.comment-container textarea {
  margin-top: 20px;
  padding-bottom: 50px;
  border: none; }

.comment-container .btn-submit {
  margin: 20px 0 5px 0;
  background-color: #FA9028;
  padding: 7px 15px;
  border-radius: 15px;
  font-size: 1.125em;
  border: none; }
  .comment-container .btn-submit:hover {
    background-color: #f8a757;
    color: white; }

.collapse-button {
  margin: 0 20px; }

.collapse-button h5 {
  background-color: #74E0D4;
  padding: 10px 15px;
  border-radius: 15px;
  font-size: 1.125em; }
  .collapse-button h5:hover {
    background-color: white;
    border: 1px solid #74E0D4;
    color: #74E0D4; }

